import groupNav from "@/components/mycom/groupNav.vue"
import groupInfoList from "@/components/mycom/groupInfoList.vue"
export default {
	components: {
		groupNav,
		groupInfoList
	},
	data() {
		return {
			id: '',
			navList: [{
					type: 1,
					value: '在招职位'
				},
				{
					type: 2,
					value: '公司信息'
				}
			],
			page: 1,
			totalPage: 1,
			name: ''
		}
	},
	onLoad(options) {
		this.id = options.id;
		this.name = options.name;
		this.$nextTick(function() {
			this.loadData()
		})
	},
	onReachBottom() {
		this.status = 'loading';
		this.totalPage > this.page ?
			((this.page += 1), this.loadData()) :
			setTimeout(() => {
				this.status = 'noMore';
			}, 300);
	},
	methods: {
		loadData() {
			let parmas = {
				id: this.id,
				pageNo: this.page,
				pageCount: "10",
			};
			console.log(parmas);
			this._reqw
				.ipost(parmas, 'worksList')
				.then(res => {
					res.result == 0 ?
						((res.totalPage > 1 ? this.status = 'more' : this.status = 'noMore'), this.totalPage = res.totalPage,
							this.$refs.groupInfoList.setData(res.dataList)
						) :
						this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		},
		navClick(e) {
			if (e == 1) {
				this.$api.navTo(`/pages/groupDetails?id=${this.id}`)
			}
		}
	}
}
